/*-------------------------------------------------*
/* =  Navigation
/*-------------------------------------------------*/
.sf-menu,
.sf-menu * {
  margin: 0;
  padding: 0;
  list-style: none; }

.sf-menu {
  line-height: 1.0; }

.sf-menu ul {
  position: absolute;
  top: -999em;
  width: 180px;
  /* left offset of submenus need to match (see below) */ }

.sf-menu ul li {
  width: 100%; }

.sf-menu li:hover {
  visibility: inherit;
  /* fixes IE7 'sticky bug' */ }

.sf-menu li {
  float: left;
  position: relative; }

.sf-menu a {
  display: block;
  position: relative; }

.sf-menu li:hover ul,
.sf-menu li.sfHover ul {
  left: 10px;
  top: 40px;
  /* match top ul list item height */
  z-index: 99; }

ul.sf-menu li:hover li ul,
ul.sf-menu li.sfHover li ul {
  top: -999em; }

ul.sf-menu li li:hover ul,
ul.sf-menu li li.sfHover ul {
  left: 180px;
  /* match ul width */
  top: 0; }

/*** skin ***/
.sf-menu {
  float: left; }

.sf-menu a {
  display: block;
  float: left;
  color: #000000;
  padding-right: 28px;
  text-decoration: none;
  text-transform: uppercase; }

.sf-menu > li > a:focus,
.sf-menu > li > a:hover,
.sf-menu > li > a:active,
.sf-menu > li.sfHover > a,
#masternav .current-menu-item > a:first-child {
  color: #a9b6b6; }

/*Subs*/
.sf-menu ul {
  padding: 0;
  font-size: 12px;
  background: #ffffff;
  border: 1px solid #ddd; }

.sf-menu ul ul {
  margin-top: -1px; }

.sf-menu ul a {
  margin: 0;
  line-height: normal;
  height: auto;
  padding: 10px;
  display: block;
  float: none;
  color: #999;
  font-weight: normal;
  text-transform: none; }

.sf-menu ul a:focus,
.sf-menu ul a:hover,
.sf-menu ul a:active {
  color: #000000;
  background: #f9f9f9; }

.sf-menu ul .current-menu-item a {
  color: #ffffff; }

.sf-menu ul li {
  border-bottom: 1px solid #ddd; }

.sf-menu ul li:last-child {
  border-bottom: 0px; }

/*-------------------------------------------------*
/* =  Overlay Navigation
