.nav--coaching {
  background-color: $white;
  text-align: center;
  padding: 30px 20px;
}

.coaching-page {
  h2 {
    font-family: $fjalla;
    font-style: normal;
    text-transform: uppercase;
    font-size: 26px;
    line-height: 1.25;
    letter-spacing: 1px;
    margin-bottom: 16px;

    em {
      font-family: $cardo;
      font-style: italic;
      text-transform: none;
      letter-spacing: normal;
    }
  }

  h3 {
    font-family: $fjalla;
    font-style: normal;
    text-transform: uppercase;
    font-size: 22px;
    letter-spacing: 1px;
    margin-bottom: 16px;

    em {
      font-family: $cardo;
      font-style: italic;
      text-transform: none;
      letter-spacing: normal;
    }
  }

  h4 {
    font-family: $dosis;
    font-style: normal;
    text-transform: uppercase;
    font-size: 16px;
    line-height: 1.125;
    letter-spacing: 3px;
    margin-bottom: 16px;
  }

  @media (max-width: 767px) {
    h1 {
      font-size: 32px;
      line-height: 1.2;
    }
    h4 {
      font-size: 14px;
      line-height: 1.25;
    }
  }
}

.secondary-header--coaching {

  background-position: top;
  background-repeat: no-repeat;

  h2, h3, h4 {
    margin-bottom: 6px;
  }

  #page-heading-inner {
    padding: 30px 30px;
  }

  @media (min-width: 768px) {
    &#page-heading {
      background-position: center;
      max-height: 500px;
    }
  }
}

.coaching-page {
  #page-heading-inner {
    text-align: center;
  }

  &.coaching-page--relationship {
    #page-heading-inner {
      text-align: left;
      max-width: 320px;
    }
  }
}

.section--padded {
  padding: 3rem 20px;
}

.coaching-page .section--padded {
  form {
    margin: 0 auto;
    text-align: center;
  }

  label {
    @include screen-reader;
  }

  .gform_body {
    width: auto;
    flex: 1 0 auto;

    ul.gform_fields {

      li.gfield {
        margin-right: 0.5rem;

        input {
          border: none;
          border-bottom: 1px solid $blue;
          border-radius: 0;
          background-color: transparent;
          padding: 0;
          padding-bottom: 2px;
          text-indent: 10px;
          font-family: $cardo !important;
          font-size: 14px;
          line-height: normal;
          font-weight: 400;
          font-style: italic;
          color: $black;

          &::placeholder {
            color: $black;
          }
        }
      }
    }
  }

  .gform_footer {
    width: auto;
    flex: 0 1 auto;

    input[type="submit"] {
      background-color: $blue;
      color: $white;
      font-family: $fjalla;
      font-size: 12px !important;
      line-height: 1 !important;
      letter-spacing: 1px;
      padding: 12px 35px;
      border: 0;
      text-transform: uppercase;
      height: auto !important;
      margin: 8px 0;
      transition: opacity 250ms ease;

      &:hover {
        opacity: 0.8;
      }
    }
  }

  @media (min-width: 641px) {
    form {
      display: flex;
      flex-wrap: wrap;
      justify-content: center;
      align-items: center;

      ul.gform_fields {
        display: flex;

        li.gfield {
          flex: 1 0 auto;
        }
      }
    }

  }

}

.coaching-page {
  .wp-block-group {
    &.section--padded {
      .wp-block-group__inner-container {
        max-width: 840px;
        margin: 0 auto;
      }
    }
  }

  .has-blue-background-color,
  .has-black-background-color {
    .gform_body ul.gform_fields li.gfield input {
      border-bottom: 1px solid $white;
      color: $white;

      &::placeholder {
        color: $white;
      }
    }
    .gform_footer input[type="submit"] {
      background-color: $white;
      color: $blue;
    }
  }
}

// Password-Protected Form
.gate {
  padding: 3rem 20px;
  display: flex;
  align-content: center;
  justify-content: center;
  text-align: center;

  label {
    font-family: $dosis;
    text-transform: uppercase;
    letter-spacing: 1.5px;
    display: block;
    margin-bottom: 6px;
  }

  input[type="submit"] {
    padding: 10px 16px 9px;
    color: $white;
  }
}

// embedded videos
.wp-block-embed {
  iframe {
    position: absolute;
    top: 0;
    right: 0;
    bottom: 0;
    left: 0;
    width: 100%;
    max-width: 100%;
    height: 100%;
  }

  .wp-block-embed__wrapper {
    position: relative;
    display: block;
    padding-top: 50%; // Default to 2:1 aspect ratio.
  }

  // Add responsiveness to common aspect ratios.
  &.wp-embed-aspect-21-9 .wp-block-embed__wrapper {
    padding-top: 42.85%; // 9 / 21 * 100
  }

  &.wp-embed-aspect-18-9 .wp-block-embed__wrapper {
    padding-top: 50%; // 9 / 18 * 100
  }

  &.wp-embed-aspect-16-9 .wp-block-embed__wrapper {
    padding-top: 56.25%; // 9 / 16 * 100
  }

  &.wp-embed-aspect-4-3 .wp-block-embed__wrapper {
    padding-top: 75%; // 3 / 4 * 100
  }

  &.wp-embed-aspect-1-1 .wp-block-embed__wrapper {
    padding-top: 100%; // 1 / 1 * 100
  }

  &.wp-embed-aspect-9-6.wp-block-embed__wrapper {
    padding-top: 66.66%; // 6 / 9 * 100
  }

  &.wp-embed-aspect-1-2.wp-block-embed__wrapper {
    padding-top: 200%; // 2 / 1 * 100
  }
}